#earth-wrapper{
    width: 50dvw;
    height: 50dvw;
    position: fixed;
    right: -5dvw;
    bottom: -5dvw;
    filter: blur(4px);
    transition: all 1s;
    z-index: -10;

    video, #earth-canvas{
        width: 100%;
        position: absolute;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
    }
}
#earth-wrapper:hover{
    filter: blur(1px);
}

#main-title{
    font-weight: 800;
}
#sub-title{
    font-weight: bolder;
}
#landing-sec{
    padding-top: 50dvh;
    height: 100dvh;
}
#description-sec{
    height: 50dvh;
}

#description{
    max-width: 700px;
    width: max-content;
    position: relative;
    padding: 2rem 3rem 7rem 3rem;
}

#learn-more-btn{
    position: absolute;
    bottom: 1rem;
    right: 2rem;
}

#btns-wrapper{
    margin-top: 2rem;
    display: flex;
    flex-direction: row;
    gap: 1rem;
    border: 1px solid var(--glow-dim-color);
    width: max-content;
    padding: 1rem;
    border-radius: 0.5rem 10rem 10rem 10rem;
    
}

.cta{
    border: 1px solid transparent;
    background-color: var(--theme-glass-color);
    text-shadow: 0px 0px 4px var(--glow-color);
}
.cta:hover{
    background-color: transparent;
    border: 1px solid var(--glow-color);
    box-shadow: 0px 0px 10px var(--theme-color);
}

#line-sec{
    height: 50rem;
    border-top: 1px solid var(--glow-color);
    border-bottom: 1px solid var(--glow-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5rem;
    margin: 4rem auto;
}

#line-wrapper{
    position: relative;
    height: 20rem;
    width: 100%;
    margin-top: 5rem;
}

.line-logo{
    width: 100%;
    max-width: 300px;
    min-width: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0.8;
    z-index: 5;
}

.line-glow{
    filter: blur(5px) invert(71%) sepia(69%) saturate(5374%) hue-rotate(108deg) brightness(101%) contrast(95%);
    opacity: 0.5;
    transition: all 0.5s;
    
    z-index: 3;
}

#line-title{
    font-size: 3rem;
    font-weight: 600;
}

#line-link:hover~.line-glow{
    filter: blur(20px) invert(71%) sepia(69%) saturate(5374%) hue-rotate(108deg) brightness(101%) contrast(95%);
}
#line-link:hover~.line-logo{
    opacity: 0.9;
}

#settlement-card{
    max-width: 600px;
    margin: auto;
    padding: 5rem;
    margin: 4rem auto;
}

#settlement-dis{
    margin-top: 3rem;
    font-size: 1.5rem;
}

#cards-sec{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    gap: 1rem;
    padding: 2rem;
}

.qa-card{
    padding: 2rem;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 30rem 5rem 1fr;
}

.qa-title{
    font-size: 1.8rem;
}

.qa-img{
    object-fit: contain;
    object-position: bottom;
    margin: 2rem 2rem 4rem 2rem;
    width: calc(100% - 4rem);
    height: calc(100% - 6rem);
}

#delivery-sec{
    display: flex;
    flex-direction: column;
    padding: 10rem 2rem;
    margin: 10rem auto;
    gap: 5rem;
    border-top: 1px solid var(--text);
    border-bottom: 1px solid var(--text);
}

#delivery-cards{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
}

.delivery-card{
    width: 100%;
    transform: scale(80%);
    transition: all 0.8s;
    padding: 5rem 2rem;
}
.delivery-card-focus{
    transform: scale(100%);
}
.delivery-card-focus#profit-card{
    background-color: #FF585820;
    box-shadow: 0px 0px 20px 5px #FF5858;
}
.delivery-card-focus#loss-card{
    background-color: #58FF9B20;
    box-shadow: 0px 0px 20px 5px #58FF9B;
}
#delivery-amount{
    border: 1px solid var(--border-color);
    border-radius: 2rem;
    padding: 2rem 3rem 3rem 3rem;
    width: max-content;
    margin: auto;
}
#delivery-amount{
    font-size: 1.3rem;
}

#cta-bottom{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    gap: 1rem;
    padding: 1rem;
    margin: 5rem auto 8rem auto;
    width: 100%;
}

.arrow-btn{
    width: 100%;
    display: flex;

}