html{
  font-size: 14px;
  font-weight: 500;
  /* scrollbar-color: #c2c2c2 #fff;
  scrollbar-width: thin; */
}

::-webkit-scrollbar{
  width: 10px;
}

::-webkit-scrollbar-track{
  background: #fff;
  width: thin;
}


::-webkit-scrollbar-thumb {
  background: #c2c2c2; /* 滑块颜色 */
  border-radius: 10px; /* 圆角 */
  border: 2px solid #fff;
}

:root{
  --theme-color: #268dcb;
  --theme-glass-color: #268dcb50;
  --background-color: #06080E;
  --background-gradient: linear-gradient(150deg, #06080E, #17283C, #06080E, #122133);
  --title: #ffffff;
  --text: #f5f5f5;
  --border-color: #c5c5c5;
  --glass-color: #ffffff10;
  --shadow-color: #00000050;
  --glow-color: #ffffff;
  --glow-dim-color: #ffffff50;
  --dark-glass-color: #00000030;
}

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  font-family: "Outfit", sans-serif;
}

#root{
  box-sizing: content-box;
}

body{
  box-sizing: content-box;
  background-color: var(--background-color);
  color: var(--title);
  height: 100dvh;
  width: 100dvw;
  background-image: var(--background-gradient);
  background-attachment: fixed;
  background-size: 400%;
  animation: bg-anim 10s ease-in-out infinite alternate;
  overflow-x: hidden;
}


@keyframes bg-anim{
  0%  {background-position: left;}
  100%  {background-position: right;}
}

h1{
  font-size: 5rem;
  font-weight: 400;
}

h2{
  font-size: 3rem;
  letter-spacing: 0.2rem;
  font-weight: 700;
}

h3{
  font-size: 2rem;
  letter-spacing: 0.3rem;
  font-weight: 400;
}

h4{
  font-size: 1.8rem;
  font-weight: 300;
  letter-spacing: 0.2rem;
}

p{
  letter-spacing: 0.1rem;
  line-height: 200%;
  margin-top: 1rem;
}

img{
  width: 100%;
}

button{
  padding: 1rem 2rem;
  border-radius: 10rem;
  background-color: transparent;
  border-style: none;
  color: var(--text);
  box-shadow: 0px 0px 4px var(--shadow-color);
  backdrop-filter: blur(10px);
  font-size: 1.2rem;
  letter-spacing: 0.2rem;
  cursor: pointer;

  transition: all 0.5s;
}
button:hover{
  background-color: var(--glass-color);
  box-shadow: 0px 0px 4px var(--glow-color);
}

legend{
  font-size: 1.8rem;
  padding: 0rem 2rem;
}

.pointer{
  cursor: pointer;
}

.link-wrapper{
  position: relative;
}
.link-sensor{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 99;
}

.no-style{
  list-style-type: none;
}

.reading-sec{
  max-width: 1024px;
  width: calc(100% - 2rem);
  margin: auto;
  padding: 1rem
}

.card{
  box-shadow: 0px 0px 4px var(--glow-color);
  background-color: var(--glass-color);
  padding: 1.5rem;
  border-radius: 1rem;
  backdrop-filter: blur(5px);
}

.text-center{
  text-align: center;
}

.text-glow{
  text-shadow: 0px 0px 10px var(--theme-color);
}